@import '../../../shared/mixins';
@import '../../../shared/variables';

#mobileTable{
    padding: 0;
    border: 1vh solid blue;
    border: 1vh solid transparent;
    height: 100vh;
    width: 98vw;

    td {
        border: 2px solid magenta;
        border: 2px solid transparent;
        height: 25%; /* of parent */
        width: 33vw;
    
        a {
            text-decoration: none;
        }
    }

    .circle {
        width: 100%;
        height: 100%;
        font-weight: bold;
        border-radius: 100%;
        color: $dark-magenta;
        background: $default-gradient-radial;
        // filter: blur(1px);
        
        display: flex;
        align-items: center;
        justify-content: center;
    
        &:hover {
            box-shadow: 10px 10px 5px lightblue;
        }
    
    }
    
    .italics {
        font-style: italic;
        text-align: center;
    }
}